import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useSchoolTypes() {
  const schoolTypes = ref([]);
  const fetchSchoolTypes = async () => {
    const { data } = await InfoService.fetchSchoolTypes();
    schoolTypes.value = data;
  };
  onMounted(async () => {
    await fetchSchoolTypes();
  });

  return {
    schoolTypes,
  };
}
