import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useJuvenileStatuses() {
  const juvenileStatuses = ref([]);
  const fetchJuvenileStatuses = async () => {
    const { data } = await InfoService.fetchJuvenileStatuses();
    juvenileStatuses.value = data;
  };
  onMounted(async () => {
    await fetchJuvenileStatuses();
  });
  return {
    juvenileStatuses,
  };
}
