<template>
  <section class="bg-white rounded-md p-3">
    <PageTitle> Bolalar ro'yxati </PageTitle>
    <TableView v-model:current-page="currentPage" v-model:page-size="pageSize" :page-size-options="pageSizeOptions"
      :all-pages-count="allPagesCount" @table-options-changed="fetchTeenagers">
      <template #filter-additional-content>
        <div class="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div class="form_wrapper my-3">
            <input v-validate-cyrillic id="search" v-model="search" placeholder="F.I.SH bo'yicha qidirish"
              class="base_input" name="search" type="text" />
          </div>
          <template v-if="!isSuperuserOrDirectorRole">
            <div class="form_wrapper my-3">
              <SelectBox id="address_region" v-model="address_region"
                         :options="addEmptyItemToSelectAllFilterOptions(regionsList, 'name', 'id', 'Barcha viloyatlar bo\'yicha')"
                         show-by="name" track-by="id" placeholder="Viloyat bo'yicha" />
            </div>
          </template>
          <div class="form_wrapper my-3">
            <SelectBox id="school_type" v-model="school_type"
              :options="addEmptyItemToSelectAllFilterOptions(schoolTypes, 'text', 'id', 'Barcha muassasa turlari bo\'yicha')"
              show-by="text" track-by="id" placeholder="Ta`lim muassasa bo'yicha" />
          </div>
          <div class="flex items-center gap-2 my-3">
            <SelectBox id="status" v-model="status"
              :options="addEmptyItemToSelectAllFilterOptions(juvenileStatuses, 'text', 'id', 'Barcha holatlar bo\'yicha ')"
              show-by="text" track-by="id" placeholder="Holatlar bo'yicha" />
            <button @click="$router.go()" class="base_input !w-auto">
              <Icon icon="mdi:close" />
            </button>
          </div>
        </div>
      </template>
      <template v-if="loading" #loading>
        <Loading v-model:active="loading" :can-cancel="false" :is-full-page="false" color="#4785FE" loader="dots" />
      </template>
      <template v-else #table-content>
        <TableJuveniles :table-body="teens" @sort-by-date="sortTeensByDate" />
      </template>
    </TableView>
  </section>
</template>

<script setup>
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import TableJuveniles from '../components/Tables/TableJuveniles';
import {computed, ref, watch} from 'vue';
import TableView from '../components/Tables/TableView';
import useSchoolTypes from '../composables/selectbox-list/useSchoolTypes';
import useRegion from '../composables/selectbox-list/useRegion';
import useTableOptions from '../composables/useTableOptions';
import useJuvenileStatuses from '../composables/selectbox-list/useJuvenileStatuses';
import SelectBox from '../components/Common/SelectBox';
import { addEmptyItemToSelectAllFilterOptions } from '../utils/selectboxOptionsHelper';
import PageTitle from '../components/LayoutItems/PageTitle';
import JuvenilesService from '../services/juveniles.service';
import useErrorAlert from '../composables/useErrorAlert';
import { Icon } from '@iconify/vue';
import {useStore} from "vuex";

const { fetchJuvenileReportsList } = JuvenilesService;

const { regionsList } = useRegion();
const { schoolTypes } = useSchoolTypes();
const { juvenileStatuses } = useJuvenileStatuses();
const { currentPage, pageSize, allPagesCount, pageSizeOptions } = useTableOptions([10, 15, 20]);

const teens = ref([]);
const loading = ref(true);
const { alertThisError } = useErrorAlert();

const dateFilterType = ref('ASC' || 'DESC');
const school_type = ref('');
const address_region = ref('');
const search = ref('');
const status = ref('');
const SUPERUSER_ROLE = 'Apparat';
const DIRECTOR_ROLE = 'Direktor';


const store = useStore();
const isSuperuserOrDirectorRole = computed(() => store.getters['getUserInfo'].role === SUPERUSER_ROLE || store.getters['getUserInfo'].role === DIRECTOR_ROLE);
const fetchTeenagers = async (params) => {
  try {
    loading.value = true;
    const resp = await fetchJuvenileReportsList({ ...params });
    teens.value = resp?.results;
    allPagesCount.value = resp?.num_pages;
    currentPage.value = resp?.current;
    loading.value = false;
  } catch (e) {
    alertThisError(e);
  }
};

watch(
  [search, school_type, address_region, pageSize, status],
  async ([srch, stype, distrctId, psize, status]) => {
    await fetchTeenagers({
      full_name: srch,
      school_type: stype,
      address_region: distrctId,
      page_size: psize,
      page: 1,
      status,
    });
  }
);
watch(
  () => currentPage.value,
  async (page) => {
    await fetchTeenagers({
      full_name: search.value,
      school_type: school_type.value,
      address_region: address_region.value,
      page_size: pageSize.value,
      page: page,
      status: status.value,
    });
  },
  {
    immediate: true,
  }
);

function sortTeensByDate() {
  if (dateFilterType.value === 'ASC') {
    dateFilterType.value = 'DESC';
    teens.value.sort(function (a, b) {
      return new Date(b.birth_date) - new Date(a.birth_date);
    });
  } else {
    dateFilterType.value = 'ASC';
    teens.value.sort(function (a, b) {
      return new Date(a.birth_date) - new Date(b.birth_date);
    });
  }
}
</script>
<style scoped></style>
